import { downloadImage, previewImage, chooseImage, uploadImage, getLocalImgData } from '@utils/ydk';

import html2canvas from 'html2canvas';

export default {
  data() {
    return {
      img: '',
      isShow: false,
    };
  },
  methods: {
    async onclickSave() {
      // const xxId = 'https://oimagea7.ydstatic.com/image?id=5922902393307803720&product=xue';
      // const { localId } = await downloadImage(xxId); // 下载普通资源文件没有问题  可以正常展示出来(localdata)
      // this.isShow = true;
      // 模拟长按事件
      $('#full-img').trigger('longTap');
      // const imgDom = document.getElementById('full-img');
      // creatTouchstart(imgDom);
      // setTimeout(() => {
      //   creatTouchend(imgDom)
      // }, 200)
      // setTimeout(() => {
      //   creatTouchmove(imgDom)
      // }, 200)
      // setTimeout(() => {
      //   creatTouchmove(imgDom)
      // }, 200)

      wx.showMenuItems({
        menuList: ['menuItem:favorite'] // 要显示的菜单项，所有menu项见附录3
      });
      // wx.closeWindow()
    },
    onClickHide() {
      this.isShow = false;
    }
  },
  mounted: async function () {

    const canvas = await html2canvas(document.getElementById('target'));
    const fullQuality = canvas.toDataURL("image/jpeg", 1.0);//转成base64
    this.img = fullQuality;

    $(() => {
      wx.showAllNonBaseMenuItem();
    })

  }
};

function creatTouchstart(el) {
  var event = document.createEvent('Events');
  event.initEvent('touchstart', true, true);
  el.dispatchEvent(event);
}

function creatTouchmove(el) {
  var event = document.createEvent('Events');
  event.initEvent('touchmove', true, true);
  el.dispatchEvent(event);
}



