import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home/index.vue'
import Topic from '@/components/Topic/index.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/topic',
      name: 'Topic',
      component: Topic
    }
  ]
})



